<template>
	<div id="shop-select">
		<public-header title="选择店铺"></public-header>
		<div class="content">
			<div class="shop-item flex-lr" v-for="item in stores" :key="item.id" @click="onSelectShop(item.id)">
				<div class="info flex">
					<div class="logo-wrap">
						<img class="img-100" :src="item.logo" alt="">
					</div>
					<div class="title ellipsis">{{item.title}}</div>
				</div>
				<i class="icon icon-right font-16 c-gray"></i>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				query: {},
				stores: [],
				showPreLoading: true,
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'errander/store/index',
					data: {
						nosid: 1
					},
					success: (res) => {
						let stores = res.stores;
						if(stores && stores.length == 1) {
							setData({
								vue: that,
								url: 'errander/store/select',
								data: {
									nosid: 1,
									sid: stores[0].id,
								},
								success: (res) => {
									if(that.query && that.query.mode) {
										that.util.jsUrl('/plugin/pages/paotui/store/order/diy', that.query, 'replace');
										return false;
									}
									that.util.jsUrl('/plugin/pages/paotui/store/order/index');
								}
							});
							return;
						}
						if(stores && stores.length > 1) {
							that.stores = [...that.stores, ...stores];
							return;
						}
					}
				})
			},
			onSelectShop(sid) {
				var that = this;
				setData({
					vue: that,
					url: 'errander/store/select',
					data: {
						nosid: 1,
						sid: sid,
					},
					success: (res) => {
						if(that.query && that.query.mode) {
							that.util.jsUrl('/plugin/pages/paotui/store/order/diy', that.query, 'replace');
							return false;
						}
						that.util.jsUrl('/plugin/pages/paotui/store/order/index');
					}
				});
			}
		},
		mounted() {
			this.query = this.util.parseQuery(this.$route.query);
			this.onLoad();
		}
	}
</script>

<style >
	#shop-select .shop-item{
		padding: 10px 15px;
		margin-top: 10px;
		background-color: #fff;
		color: #333;
	}
	#shop-select .shop-item .info{
		width: 90%;
		font-size: 15px;
	}
	#shop-select .shop-item .info .title{
		flex: 1;
	}
	#shop-select .shop-item .logo-wrap{
		width: 40px;
		height: 40px;
		font-size: 0px;
		margin-right: 10px;
		border-radius: 5px;
		overflow: hidden;
	}
</style>


